angular.module("lesson_mgmt").run(["$templateCache",function(a){"use strict";a.put("/new_platform/static/lesson_mgmt/nghtml/header-bar.html",'<!-- NAVIGATION -->\n<div ng-controller="headerController">\n    <div class="navbar navbar-default" role="navigation" style="position: fixed;top: 0px;">\n        <div class="container-fluid">\n            <div class="navbar-header" style="font-weight: 400;color:white;">\n                <hamburger-toggle ng-mouseover="hamburgerHover()" ng-mouseleave="HamburgerUnhover()" ng-click="toggleState()" style="cursor: pointer;" state="tgState" class="hamburger-toggle"></hamburger-toggle>\n                <span class="navbar-brand" style="color:white">Lesson Management</span>\n                <div style="display: inline-block;position: relative;top: 8px;left:10px;color:white;">\n                    <span style="margin-left: -10px;" ng-if="loadPartners == false">Loading...</span>\n                    <span style="margin-left: -10px;" ng-if="scheduling">Scheduling...</span>\n                    <span style="margin-left: -10px;" ng-if="scheduleDone">Just scheduled.</span>\n                </div>\n                <div class="notifications">\n                    <span ng-if="userrole === \'Mentor\' || userrole === \'Mentee\'" class="username">[[firstname]] [[lastname.split(\'\')[0] ]]</span>\n                    <span ng-if="userrole !== \'Mentor\' && userrole !== \'Mentee\'" class="username">[[firstname]] [[lastname.split(\'\')[0] ]]</span>\n                    <!--<i class="glyphicon glyphicon-comment"></i>--></div>\n            </div>\n        </div>\n    </div>\n\n<div class="sidenav" ng-class="{open: tgState}" ng-init="Administration = false; Reports = false;" style="font-size:14px;font-weight: 400;">\n  <ul ng-if="userrole === \'Mentor\' || userrole === \'Mentee\'" class="nav nav-pills nav-stacked">\n    <li ui-sref-active="active"><a href="/canvas/#/">Current Lesson</a></li>\n    <li ui-sref-active="active"><a href="/portfolio/#/">Portfolio</a></li>\n    <li ng-if="(match_status === \'M\' && userrole === \'Mentor\') || userrole === \'Mentee\'" ui-sref-active="active"><a href="/cchklist/list/#/">College Checklist</a></li>\n    <li><a href="/events/personal/">Calendar</a></li>\n    <!--<li><a href="/iuser/myinfo/{{persona}}/" >Profile</a></li>-->\n    <!--<li ng-show="userrole === \'Mentee\'"><a href="https://learn.imentor.org/help/article/link/imentor-curriculum">Resources</a></li>-->\n    <li ng-show="userrole !== \'Mentee\'"><a href="https://learn.imentor.org/help/article/link/home" target="_blank">Learning Center</a></li>\n    <li><a href="/message/list/">Legacy Platform</a></li>\n    <li ui-sref-active="active"><a href="https://imentor.org/privacy-policy">iMentor Privacy Policy</a></li>\n    <li ui-sref-active="active"><a  href="" ng-click="logout();">Sign out</a></li>\n  </ul>\n  <ul ng-if="userrole !== \'Mentor\' && userrole !== \'Mentee\'" class="nav nav-pills nav-stacked">\n    <li ui-sref-active="active"><a href="/canvas/#/">Current Lesson</a></li>\n    <li ui-sref-active="active"><a href="/portfolio/#/">Portfolio</a></li>\n    <li ui-sref-active="active"><a href="/lesson_mgmt/#/">Lesson Management</a></li>\n    <li><a href="/events/admin/">Calendar</a></li>\n    <li><a href="/message/list/note/">Add a Note</a></li>\n    <!--<li><a href="/iuser/backend_profile/{{persona}}/" >Profile</a></li>-->\n    <li><a ng-click="Administration = !Administration" style="cursor:pointer"><span class="fa fa-caret-down" style="position: relative;top: 3px;margin-right: 3px;font-size: 14px;"></span> Administration </a></li>\n    <ul ng-show="Administration === true" class="submenu">\n      <li><a href="/iuser/" >Users</a></li>\n      <li><a href="/partners/list/">Partner Sites</a></li>\n      <li><a href="/attendance/">Attendance</a></li>\n      <!--<li><a href="/survey/">Surveys</a></li>-->\n      <li><a href="/new_surveys/">Surveys</a></li>\n      <li ng-show="subdomain != \'mentor2\' && subdomain != \'bbsu\' && subdomain != \'bbbscharlotte\' && subdomain != \'bbbsmilwaukee\' && subdomain != \'bbbswillgrundy\'"><a href="/enrollment/">Mentee Enrollment</a></li>\n      <li ng-show="subdomain === \'platformtraining\'"><a href="/mentor_rubric/">Mentor Rubric</a></li>\n      <!--<li><a href="/reporting/manage_alerts/">Alerts</a></li>-->\n    </ul>\n    <li><a ng-click="Reports = !Reports" style="cursor:pointer"><span class="fa fa-caret-down" style="position: relative;top: 3px;margin-right: 7px;font-size: 14px;"></span>Reports</a></li>\n    <ul ng-show="Reports === true" class="submenu">\n      <li><a href="/snapshot/">Snapshot</a></li>\n        <li ng-show="isOnPartnerListTableau && (subdomain == \'nyc\' || subdomain == \'chicago\' || subdomain == \'bayarea\' || subdomain == \'127\')"><a ng-href="[[programUrl]]" target="_blank" style="cursor: pointer;" ng-click="getProgramParticipation([[subdomain]]);">S.T.E.V.E.</a></li>\n        <li ng-show="isOnPartnerListTableau && (subdomain != \'nyc\' && subdomain != \'chicago\' && subdomain != \'bayarea\' && subdomain != \'127\') && userrole != \'Partner Admin\'"><a ng-href="[[programUrl]]" target="_blank" style="cursor: pointer;" ng-click="getProgramParticipation([[subdomain]]);">Participation History</a></li>\n        <li><a href="/flipbook/">Flipbook</a></li>\n      <li><a href="/reporting/intake_application_tracker/">Intake App Tracker</a></li>\n      <li><a href="/iuser/screening_tracker/">Screening Tracker</a></li>\n      <!--<li><a href="/reporting/participation_tracker/">MPT</a></li>-->\n      <!--<li><a href="/reporting/email_tracker/">Email Tracker</a></li>-->\n      <!--<li><a href="/reporting/email_log/">Email Log</a></li>-->\n      <li><a href="/reporting/event_tracker/">Event Tracker</a></li>\n      <li><a href="/reporting/event_log/">Event Log</a></li>\n      <!--<li><a href="/ratings/tracker/pairs/2/">Ratings Tracker</a></li>-->\n      <li><a href="/notes/support_tracker_pair/">Support Tracker</a></li>\n      <li><a href="/notes/note_log/">Note Log</a></li>\n    </ul>\n    <li><a href="https://learn.imentor.org/" target="_blank">Learning Center</a></li>\n    <li><a href="/message/list/">Legacy Platform</a></li>\n    <li ui-sref-active="active"><a href="https://imentor.org/privacy-policy">iMentor Privacy Policy</a></li>\n    <li ui-sref-active="active"><a href="" ng-click="logout();">Sign out</a></li>\n  </ul>\n</div>\n</div>\n'),a.put("/new_platform/static/lesson_mgmt/nghtml/schedule.html",'<div ng-controller="MainCtrl">\n<h2 class="admin-heading">Manage your lessons</h2>\n    <p class="questions"> Which group of students is this lesson for?</p>\n\n      <div\n        isteven-multi-select\n        input-model="classes"\n        output-model="selectedPartnerSites"\n        button-label="name"\n        item-label="name"\n        tick-property="ticked"\n        class="partner-sites"\n        max-height="200px"\n        max-labels="1"\n        on-item-click="updatePartner()"\n        on-select-all="updatePartner()"\n        on-select-none="resetPartner()"\n        on-reset="resetPartner()"\n        translation="partnerSitesLang"\n        selection-mode="single"\n        ></div>\n\n    <div class="margin-20"></div>\n\n    <p ng-if="selectedPartner && scheduledLessons.length > 0"class="questions">Below are all lessons scheduled for [[selectedPartner.name]]</p>\n    <table ng-if="selectedPartner && scheduledLessons.length > 0" class="tableChrt" style="width: 700px;">\n        <tr class="table-header table-header-top">\n            <th colspan=\'12\' class="text-bottom manage-col text-center ">\n                <span dropdown on-toggle="toggled(open)" class="pull-left dropdown"\n                >\n                  <a href dropdown-toggle style="" ng-class="{\'disabled\': !selectedLessonId}">\n                      Manage <i class="fa fa-wrench"></i>\n                  </a>\n                <ul class="enrollment-dropdown dropdown-menu" aria-labelledby="simple-dropdown"  ng-show="selectedLesson">\n                    <li ng-hide="showDelete === true">\n\n                        <a href class="" ng-click="showDeleteBox()">\n                            Delete this lesson\n                        </a>\n\n                    </li>\n                </ul>\n                </span>\n                <div ng-show="showDelete === true" class="lessonDeleteConfirmBox">\n                    <p>Enter the word "delete" as a confirmation:<br/>\n                        <input type="text"  ng-model="$parent.deleteText" class="deleteInput"/><br/>\n                        <a  href ng-click="cancelDelete()" style="">Cancel</a>\n                        <a  href ng-click="confirmDelete()" style="">Confirm</a>\n                    </p>\n                </div>\n            </th>\n        </tr>\n        <tr>\n            <th class="tblHeader tblHeader1"><span class="glyphicon glyphicon-ok" style="margin-right: 5px;float: left;"></span></th>\n            <th class="tblHeader">Start</th>\n            <th class="tblHeader">End</th>\n            <th class="tblHeader2">Lesson</th>\n        </tr>\n        </table>\n    <div class="tableContainer" ng-if="selectedPartner && scheduledLessons.length > 0">\n        <table class=" table table-striped tableChrt" style="width: 700px;">\n\n        <tr ng-repeat="lesson in scheduledLessons"  ng-class="{\'selected\': selectedLessonId == lesson.curriculum_id}">\n            <td class="text-center tblCell tblCell1"  ng-click="updateLessonSelection(lesson)"\n             >\n                <span class="fa fa-flag flag-ok"  ng-class="{\'selected\': selectedLessonId == lesson.curriculum_id, \'unselected\': selectedLessonId != lesson.curriculum_id ,\' fa fa-check-square-o\': selectedLessonId == lesson.curriculum_id, \'fa fa-square-o\':selectedLessonId == lesson.curriculum_id != true}" />\n\n\n            </td>\n\n\n            <td class="tblCell tblCell2">[[lesson.lesson_start_string]]</td>\n            <td class="tblCell tblCell2">[[lesson.lesson_end_string]]</td>\n            <td class="tblCell tblCell3">[[lesson.lesson_description]] ([[lesson.grade_number]].[[lesson.unit_number]].[[lesson.lesson_number]]) </td>\n        </tr>\n    </table>\n        </div>\n    <div ng-if="scheduledLessons.length < 1 && selectedPartner">\n        <p style="padding: 10px;\n    background-color: #EEEEEE;\n    color: gray;\n    width: 565px;">You havent scheduled any lessons yet for [[selectedPartner.name]].</p>\n    </div>\n\n    <div class="margin-20"></div>\n\n<p class="questions"> Choose a lesson to schedule</p>\n<div>\n    <select ng-model="selectedGrade"\n            ng-options="item.grade.grade_number as item.grade.description + \' (\' + item.grade.grade_number + \')\' for item in curriculum | unique:\'grade.grade_number\' | orderBy:[\'grade.grade_number\', \'grade.description\']"\n            ng-change="updateGrade(selectedGrade)"\n            >\n        <option value="">Grade</option>\n    </select>\n\n\n    <!--ng-options="item as item.unit.description for item in curriculum"-->\n    <select ng-show="selectedGrade"\n            ng-model="selectedUnit"\n            ng-options="item.unit.id as item.unit.description + \' (\' + item.unit.unit_number + \')\' for item in curriculum | property:\'grade.grade_number\':selectedGrade | unique: \'unit.description\' | orderBy:[\'unit.unit_number\',\'unit.description\' ]"\n            ng-change="updateUnit(selectedUnit)"\n            >\n        <option value="">Unit</option>\n    </select>\n\n\n    <select ng-show="selectedUnit"\n            ng-model="selectedLesson"\n            ng-options="item.id as item.description + \' (\' + item.curriculum_number + \')\' for item in curriculum | filter:selectedGrade | property:\'unit.id\':selectedUnit | property:\'grade.grade_number\':selectedGrade | orderBy:[\'curriculum_number\', \'description\']"\n            ng-change="updateLesson(selectedLesson)"\n            >\n        <option value="">Lesson</option>\n    </select>\n</div>\n\n<div class="margin-20"></div>\n\n<p class="questions">Choose when the lesson will begin</p>\n<div style="position: relative;top: -25px;">\n    <div id="date-pick" class="datepicker" date-set="[[today | date:\'MM/dd/yy\']]" date-format="MM/dd/yy" date-min-limit="[[minDate | date:\'MM/dd/yy\']]" date-max-limit="[[maxDate | date:\'yyyy/MM/dd\']]" button-prev="<i class=\'fa fa-arrow-left\'></i>" button-next="<i class=\'fa fa-arrow-right\'></i>">\n        <input style="padding:7px;color: #737373;" ng-model="lesson.sch_date" id="date-pick2" type="text" placeholder="MM/DD/YY" class="app-deadline" ng-change="updateCollege(lesson)" readonly/>\n    </div>\n\n    <timepicker class="c_timepicker" ng-model="mytime" ng-change="changed(mytime)" hour-step="hstep" minute-step="mstep" show-meridian="ismeridian"></timepicker>\n</div>\n    <button ng-hide="scheduling" ng-disabled="!selectedGrade || !selectedUnit || !selectedLesson || !selectedPartner || !lesson.sch_date || scheduling" ng-click="scheduleLesson()" class="btn-primary">Schedule the lesson</button>\n    <button ng-hide="!scheduling" ng-disabled="!selectedGrade || !selectedUnit || !selectedLesson || !selectedPartner || !lesson.sch_date || scheduling" class="btn-primary">Scheduling...</button>\n</div>\n<div class="margin-30"></div>\n<div class="margin-30"></div>\n\n'),a.put("/new_platform/static/lesson_mgmt/nghtml/scheduled.html",'<div ng-controller="scheduledController">\n    <div class="translucentBkg">\n    <h2>Congrats!</h2>\n    <h2>You scheduled your lesson.</h2>\n    <div class="margin-40"></div>\n    <div style="display: block;margin: 0 auto;text-align: center;"><button class="btn-primary" ng-click="scheduleAnother()">Manage your lessons</button></div>\n    </div>\n</div>')}]);